@import '../../curriculum/styleSheets/header.scss';
@import '../../curriculum/styleSheets/content.scss';
@import '../../curriculum/styleSheets/background.scss';
@import '../../curriculum/styleSheets/pen.scss';
@import '../../curriculum/styleSheets/eraser.scss';

@media print {
    header {
        display: none;
    }

    body {
        background-color: white;
    }

    content {
        top: -50px;

        .cv-background {    
        box-shadow: none;

            .cv-page { 
                height: 2040px; 

                .left-section { 
                    max-width: 250px;
                    min-width: 50px;

                    .category { 
                        padding: 60px 0;
                        min-width: 312px;
        
                        .category-text { 
                            color: white;
                            font-size: 1.2em;
                            max-width: 155px;
                            margin: 0 0 0 50px;
                            overflow-wrap: anywhere;
        
                            &.contact {
                                margin: 0 0 10px 80px;
                            }
                                    
                            &.title { 
                                color: white;
                                font-size: 1.5em;
                                margin: 0 40px;
                                font-weight: bold;
                                text-transform: uppercase;
                            }
                        }
                    }
    

                    .circle-container { 
                        justify-content: center;
                        display: flex;
                        position: relative;
        
                        .cv-circle { 
                            width: 200px;
                            height: 200px;
                            margin: 28px 0 40px 20px;
            
                            .text-circle { 
                                color: white;
                                font-size: 6em;
                                font-weight: bold;
                                padding: 50px 32px;
                            }
                        }
                    }
                }
                
                .right-section { 
                    max-width: 550px;
                    width: 550px;
                    min-width: 50px;
                    
                    .bars {
                        height: 39px;
        
                        &.bottom-bar { 
                            display: none;
                        }
                    }
        
                    .name-container { 
                        width: 100%;
                        height: 253px;
                        background-color: #292929;
        
                        .text-container {     
                            padding: 20px; 
        
                            .big-title { 
                                font-size: 3em;
                            }
                            
                            .med-title { 
                                padding: 20px 0;
                                font-size: 1.5em;
                            }
                        }
                    }
        
                    .information-container { 
                        max-width: 100%;
                        background-color: white;
                        padding: 1px 0 0 0;
                        
                        .content { 
                            min-height: 1308px;
                            width: 95%;
                            color: black;
                            font-weight: bold;
        
                            .text-job-title {
                                font-size: 1.5em;
                                padding: 0 0 5px 20px;
                            }
                            
                            .text-employer {
                                font-size: 1.2em;
                                padding: 0 0 5px 20px;
                            }
            
                            .text-descriptions { 
                                font-weight: normal;
                                font-size: 0.9em;
                                padding: 0 30px;
                                line-height: 120%;
                            }
                        }
                    }
                }
    
            }
        }

        .shapes-pen { 
            display: none;
        }
    
        .shapes-eraser { 
            display: none;
        }
    }

}