// TODO Make exception for not supporting browser for clip-path and have shapes just not showing

content { 
    .shapes-eraser { 
        box-shadow: -19px -3px 88px 13px rgba(0, 0, 0, 0.4);
        position: absolute;
        right: 100px;
        top: 100px;
        width: 158px;
        height: 262px;
        background-color: #1f7526;

        .eraser { 
            top: -44px;
            right: 2px;
            width: 154px;
            position: absolute;
            height: 44px;
            border-radius: 30px 30px 0 0;
            background-color: white;
        }
        
        .sticker { 
            position: absolute;
            top: 25px;
            right: 25px;
            width: 106px;
            height: 195px;
            background-color: #6cad40;
        }
        
        .bottom-line { 
            position: absolute;
            bottom: 0;
            right: 0px;
            width: 158px;
            height: 60px;
            background-color: #1b4c0f;

        }
    }
}