// TODO fix view when window is super thin


content { 
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    font-family: Arial, Helvetica, sans-serif ;
    
    .cv-background {
        max-width: 1240px;
        max-height: 1800px;
        background: white;
        margin: 40px 0;
        box-shadow: -54px 47px 90px 29px rgba(0, 0, 0, 0.4);
        display: flex;
        
        .cv-page {
            display: flex;
            position: relative;
            height: 100%;

            .left-section { 
                z-index: 10;
                min-width: 330px;
                background-color: #1b4c0f;
    
                .category { 
                    padding: 40px 0;
                    min-width: 312px;
    
                    .category-text { 
                        color: white;
                        font-size: 1.2em;
                        max-width: 312px;
                        margin: 0 0 0 50px;
                        overflow-wrap: anywhere;
    
                        &.contact {
                            margin: 0 0 10px 80px;
                        }
    
                        .icon { 
                            display: flex;
                            min-width: 20%;
                            justify-content: flex-end;
                            align-self: center;
                            
                            &.maps { 
                                background-image: url('../../../atlas/curriculum/asset-maps-40x40.png');
                                width: 40px;
                                height: 40px;
                                transform: scale(0.7);
                                margin: -29px -45px;
                                background-repeat: no-repeat;
                                top: 0;
                                left: 0;
                            }
                            &.message { 
                                background-image: url('../../../atlas/curriculum/asset-message-40x40.png');
                                width: 40px;
                                height: 40px;
                                transform: scale(0.7);
                                margin: 0 0 -29px -45px;
                                background-repeat: no-repeat;
                                top: 0;
                                left: 0;
                            }
                            &.phone { 
                                background-image: url('../../../atlas/curriculum/asset-phone-40x40.png');
                                width: 40px;
                                height: 40px;
                                transform: scale(0.7);
                                margin: 0 0 -29px -45px;
                                background-repeat: no-repeat;
                                top: 0;
                                left: 0;
                            }
                            &.linkedin { 
                                background-image: url('../../../atlas/curriculum/asset-linkedin-40x40.png');
                                width: 40px;
                                height: 40px;
                                transform: scale(0.7);
                                margin: 0 0 -29px -45px;
                                background-repeat: no-repeat;
                                top: 0;
                                left: 0;
                            }
                        }
                        
                        &.title { 
                            color: white;
                            font-size: 2em;
                            margin: 0 40px;
                            font-weight: bold;
                            text-transform: uppercase;
                        }
                    }
                }
                
                .circle-container { 
                    justify-content: center;
                    display: flex;
                    position: relative;
    
                    .cv-circle { 
                        width: 240px;
                        height: 240px;
                        margin: 60px 0 110px 0;
                        border-radius: 125px;  
                        background-color: #4e822c;
        
                        .text-circle { 
                            color: white;
                            font-size: 7em;
                            font-weight: bold;
                            padding: 60px 43px;
                        }
                    }
                }
            }
    
            .right-section { 
                max-width: 900px;
                min-width: 631px;
                z-index: 1;
                
                .bars {
                    height: 40px;
                    width: 100%;
                    background:
                    linear-gradient(90deg, #1f7526 20%, #de4a1c 65%);
    
                    &.bottom-bar { 
                        display: flex;
                        position: relative;
                    }
    
                    &.category { 
                        width: 80%;
                        margin: 30px -50px;
                        transform: skew(-30deg);
                        
                        .bars-text { 
                            color:white;
                            transform: skew(30deg);
                            padding: 11px 59px;
                            font-size: 1.2em;
                            text-transform: uppercase;
                            font-weight: bold;
                        }
                        
                        &.white-sep {
                            height: 40px;
                            width: 35px;
                            transform: skew(0deg);
                            margin: 0 32px 0 0;
                            position: absolute;
                            display: inline;
                            background: white;
                            top: 0;
                            right: 0;
                        }
                    }
                }
    
                .name-container { 
                    width: 100%;
                    min-height: 200px;
                    background-color: #292929;
    
                    .text-container {     
                        padding: 20px;
    
                        .big-title { 
                            font-size: 3.5em;
                            font-weight: bold;
                            color: white;
                        }
                        
                        .med-title { 
                            color: white;
                            padding: 20px 0;
                            font-size: 2em;
                            font-weight: bold;
                        }
                    }
                }
    
                .information-container { 
                    max-width: 100%;
                    background-color: white;
                    padding: 1px 0 0 0;
                    
                    .content { 
                        min-height: 1308px;
                        width: 95%;
                        color: black;
                        font-weight: bold;
    
                        .text-job-title {
                            font-size: 1.5em;
                            padding: 0 0 5px 20px;
                        }
                        
                        .text-employer {
                            font-size: 1.2em;
                            padding: 0 0 5px 20px;
                        }
        
                        .text-descriptions { 
                            font-weight: normal;
                            font-size: 1em;
                            padding: 0 20px;
                        }
                    }
                }
            }
        }
    }
}