header {
    
    .header-before {
        background-color: rgb(168, 167, 167);
        height: auto; 
        width: 100%;
        display: flex;
        position: relative;
        align-items: center;
        
        a { 
            margin: 5px;
            width: 50%;
            display: flex;
            position: relative;
            justify-content: center;
        }
    }
}