// TODO Make exception for not supporting browser for clip-path and have shapes just not showing


content { 
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    font-family: Arial, Helvetica, sans-serif ;

    .shapes-pen { 
        box-shadow: -26px 47px 88px 13px rgba(0, 0, 0, 0.4);
        position: absolute;
        top: 300px;
        left: 80px;
        width: 44px;
        height: 916px;
        background-color: #de4a1c;
        
        .lines { 
            width: 11px;
            height: 916px;
            background-color: #ff6d41;
            position: absolute;
            margin: 0 6px;
            
            &.thin { 
                width: 6px;
                height: 916px;
                right: 0;
            }
        }

        .head { 
            position: absolute;
            height: 55px;
            width: 44px;
            top: -49px;
            clip-path: polygon(
                0 90%, 
                50% 0, 
                100% 90%, 
                80% 100%, 
                69% 90%, 
                48% 100%, 
                34% 90%, 
                10% 100%, 
                0% 90%
            );
            background-color: #de8d59;
            border-image-slice: 1;

            .lead {
                background-color: black;
                height: 30px;
                width: 44px;
                clip-path: polygon(0 33%, 50% 0, 100% 82%);
            }
        }

        .metal-ring {
            bottom: -20px;
            right: -3px;
            height: 100px;
            width: 50px;
            background-color: #6f6f6f;
            position: absolute;
        }        
        
        .eraser { 
            bottom: -50px;
            right: -2px;
            height: 100px;
            width: 48px;
            background-color: #d67474;
            border-radius: 20px;
            position: absolute;

        }
    }
}